<template>
  <v-container>
    <v-card class="mt-5">
      <v-card-title>
        <h3 class="mx-auto text-center">Meeting Requests</h3>
        <v-spacer />
        <v-btn class="primary" tile large @click="mrq = true"
          >New Request</v-btn
        >
      </v-card-title>
      <v-card-text>
        <v-simple-table>
          <thead>
            <tr>
              <th>Reason</th>
              <th>Date & Time</th>
              <th>Status</th>
              <th>Reply</th>
              <th>Link</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(m, i) in meetings" :key="i">
              <td>{{ m.reason }}</td>
              <td>
                {{ m.datetime ? new Date(m.datetime).toDateString() : "" }}
              </td>
              <td>{{ m.status ? "Approved" : "Pending" }}</td>
              <td>{{ m.reply }}</td>
              <td>
                <v-btn
                  :href="m.link"
                  target="_blank"
                  fab
                  small
                  dark
                  v-if="m.link"
                >
                  <v-icon>mdi-video-plus</v-icon>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-card-text>
    </v-card>
    <v-dialog v-model="mrq" max-width="600">
      <v-card>
        <v-card-title>
          <h2 class="mx-auto text-center">Submit a Request for Meeting</h2>
        </v-card-title>
        <v-card-text>
          <v-form ref="form" lazy-validation v-model="valid">
            <v-textarea
              outlined
              label="Reason for meeting"
              v-model="meeting.reason"
            ></v-textarea>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn
            class="mx-auto primary"
            large
            tile
            :loading="loading"
            @click="meetingRequest"
            >Submit</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      meeting: {},
      valid: true,
      loading: false,
      meetings: [],
      mrq: false,
    };
  },
  methods: {
    meetingRequest() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        fetch(
          "https://script.google.com/macros/s/AKfycbyk57x3brdOcPGJt9mgxRvZa2euu7rY25OyugpHT_AYeK50988mrWh_sLHVn14tx6xixw/exec",
          {
            method: "POST",
            mode: "no-cors",
            cache: "no-cache",
            redirect: "follow",
            body: JSON.stringify({
              type: "create",
              roll: this.user.displayName,
              ...this.meeting,
            }),
          }
        ).then(() => {
          this.loading = false;
          this.mrq = false;
          this.getMeetings();
        });
      }
    },
    getMeetings() {
      fetch(
        "https://script.google.com/macros/s/AKfycbyFiaEKIjCgIHNE5G2b8m2lZ4qkCjA20uXftNqJjsk5qtqcsfQ_0lNhU84ZQERT9SjGxg/exec?roll=" +
          this.user.displayName
      )
        .then((res) => res.json())
        .then((data) => {
          this.meetings = data.routines.sort(
            (a, b) => b.meeting_id - a.meeting_id
          );
        });
    },
  },
  created() {
    this.getMeetings();
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
};
</script>

<style lang="scss" scoped>
</style>